body {
    padding: 0;
    margin: 0 0;
    font-family: sans-serif;
  }
.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  .itemLeft {
    width: 15%;
  }
  
  .itemCenter {
      width: 70%;
  }
  
  .itemRight {
    width: 15%;
  }
  
  .navbar {
    background: black;
    box-shadow: 0 3px 6px grey, 0 3px 6px grey;
  }
  
  li {
    color: white;
  }
  
  body {
    font-size: 24px;
  }
.header{
	background-color: lightgrey;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold; 
}

h2{
	text-align: center;
	font-size: 25px;
}
.card {
    width: 250px;
    height: 250px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px grey, 0 3px 6px grey;
    border: 3px solid white;
  }
.wrapper {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 25px 0 50px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
  }
